.calculator {
}
.container {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 15px;
}
.calculator__title {
  text-align: left;
  font-family: "Geometria", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 40px;

  color: #2b2b51;
}
.calculator__flex-wrapper {
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: 920px) {
    flex-direction: column;
  }
}
.calculator__col {
  flex-basis: 45%;
}

.summary {
}
.summary__position {
  display: flex;
  box-sizing: border-box;
  height: 60px;
  padding-top: 10px;
  padding-bottom: 5px;
  margin-bottom: 20px;
  justify-content: space-between;
  border-bottom: 1px solid #e1e1e1;
  @media screen and (max-width: 920px) {
    border: 1px solid #e1e1e1;
    padding-left: 20px;
    padding-right: 40px;
  }
}

.summary__title {
  font-size: 12px;
  line-height: 17px;
  color: #686868;
  align-self: flex-end;
  @media screen and (max-width: 920px) {
    align-self: center;
  }
  @media screen and (max-width: 520px) {
    text-align: left;
  }
}
.summary__data {
  align-self: center;
  font-size: 24px;
  line-height: 30px;
  color: #333333;
  @media screen and (max-width: 520px) {
    font-size: 18px;
    line-height: 23px;
  }
}
.summary__data--primary {
  color: #0073f1;
}
